import React,{Component} from 'react';
import './room-info.css';
import DB from '../../db.json';

export default class RoomInfo extends Component{
    constructor(props){
        super(props);

    }
    getExtras(){
        let elems;
        elems = this.props.data.options.map((item) => {
            return (<li>{item}</li>)
        })
        return elems;
    }
    render(){
        return(
            <div className="container">
                <div className="room-info">
                    <div className="room-images">
                        <div className="room-image" style={{backgroundImage:`url(/img/${this.props.id}/2.jpg)`}}></div>
                        <div className="room-image" style={{backgroundImage:`url(/img/${this.props.id}/3.jpg)`}}></div>
                        <div className="room-image" style={{backgroundImage:`url(/img/${this.props.id}/4.jpg)`}}></div>
                    </div>
                    <div className="room-main-info">
                        <div className="details">
                            <h3>Описание</h3>
                            <p>{this.props.data.description}</p>

                        </div>
                        <div className="key-info">
                            <h3>Информация</h3>
                            <ul className="key-info-list">
                                <li>Цена: {this.props.data.price}</li>
                                <li>Площадь: {this.props.data.square}</li>
                                <li>Кол-во персон: {this.props.data.persons}</li>
                                <li>Питомцы: {this.props.data.pet?'Да':'Нет'}</li>
                            </ul>
                        </div>
                    </div>
                    <div className="extra-section">
                        <h3>Что входит</h3>
                        <ul className="room-extras-list">
                            {this.getExtras()}
                        </ul>
                    </div>
                </div>
            </div>
        )     
    }
}