import React,{Component} from 'react';
import './header.css';
import {Link} from 'react-router-dom';

export default class Header extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div className="container">
                <header className="header">
                        <h5 className="header-logo">LogoType</h5>
                        <ul className="menu">
                            <li className="menu-item"><Link to="/" className="link">Главная</Link></li>
                            <li className="menu-item"><Link to="/apparts" className="link">Номера</Link></li>
                            <li className="menu-item"><Link to="/contacts" className="link">Контакты</Link></li>
                            <li className="menu-item"><Link to="/feedback" className="link">Обратная связь</Link></li>
                        </ul>
                        <div className="menu-right-panel">
                            <i className="material-icons search">search</i>
                            <i className="material-icons shopping-cart">shopping_cart</i>
                        </div>  
                    
                </header>
            </div>
       
        )
    }
}