import React,{Component} from 'react';
import './app.css';
import Header from '../header/header';
import Demo from '../demo/demo';
import Footer from '../footer/footer';
import DB from '../../db.json';
import RoomInfo from '../room-info/room-info';

export default class RoomPage extends Component{
    constructor(props){
        super(props);
        
    }
    render(){
        console.log(`img/${this.props.data}/1.jpg`);
        return(
            <React.Fragment>
                <Header/>
                <Demo title={DB[this.props.data - 1].title} coast={DB[this.props.data - 1].price} btnName={'Назад к номерам'} demoUrl={`/img/${this.props.data}/1.jpg`} link={'/apparts'}/>
                <RoomInfo data={DB[this.props.data - 1]} id={this.props.data}/>
                <Footer/>
            </React.Fragment>
        )
    }
}